.container{
    margin: 50px 0;
}
.Card{
    margin: 0 2% !important;
}

.infected{
    border-bottom: 20px solid rgba(0,0,255,0.6) ;
}
.recovered{
    border-bottom: 20px solid rgba(0,255,0,0.8) ;
}
.deaths{
    border-bottom: 20px solid rgba(255,0,0,0.8) ;
}

@media(max-width:770px){
    .Card{
        margin: 2% 0 !important;
    }
}